nav{
display: flex;
align-items: center;
justify-content: space-between;
padding: 20px 80px;
background-color: rgb(255, 255, 255);
box-shadow: 0 5px 15px rgba(0,0,0,0.06);
}

.ptitre{
color:rgb(0, 0, 0);
cursor:pointer;
font-size: 35px;
letter-spacing: 1px;
}

#navbar{
    display: flex;
    align-items: center;
    justify-content: center;
}

#navbar li {
    list-style: none;
    padding: 0 20px;
    position: relative;
}

#navbar li a{
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 600;
    color: #000000;
    transition: 0.3s ease-in-out;
}

#navbar li a:hover{
    cursor: pointer;
    color: #177fcf;
}

#navbar li a:hover::after{
    content: "";
    width: 30%;
    height: 2px;
    background:#177fcf;
    position: absolute;
    bottom: -4px;
    left: 20px;
}


.btn{
background-color: #000000;
color: rgb(255, 255, 255);
text-decoration: none;
border: 2px solid transparent;
padding: 10px 25px;
font-weight: bold;
border-radius: 10px;
transition: transform .4s;
}

.btn:hover{
cursor: pointer;
transform: scale(1.2);
}

#mobile{
    display: none;
}

#mobile i{
    color: #000000;
    align-items: center;
}

@media screen and (max-width:769px){
    #navbar {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 83px;
        right: -1000px;
        width: 769px;
        height: 100v;
        background-color: rgb(235, 235, 235);
        box-shadow: 0 5px 15px rgba(0,0,0,0.1);
        padding: 40px 0 0 10px;
    }

    #navbar.active{
        right: 0px;
    }

    #navbar li{
        margin-bottom: 25px;
    }

    #mobile{
        display: block;
    }

    #mobile i{
        font-size: 24px;
        cursor:pointer;
    }
}








